import request from '@/utils/request'

export function getUserinfo() {
  return request({
    url: '/users',
    method: 'get',
    params: {
      uid: 10001
    }
  })
}

export function login(data) {
  return request({
    url: '/login',
    method: 'post',
    data
  })
}

export function logout() {
  return request({
    url: '/logout',
    method: 'post'
  })
}
